<template>
  <div class="app-container">
    <div class="head"></div>
    <!-- <div class="conter">
      <div class="item-wrapper" v-for="(item,index) in 4" :key="index" @click="routerPush(index)">
        <div class="image">
          <img :src="`../../../../static/image/pc/item${index}.png`" alt />
        </div>
        <div class="font" v-show="index===0"></div>
        <div class="font" v-show="index===1"></div>
        <div class="font" v-show="index===2"></div>
        <div class="font" v-show="index===3"></div>
      </div>
    </div>-->
    <div class="content">
      <div class="arrow-left">
        <i class="el-icon-arrow-left"></i>
      </div>
      <div class="conter">
        <div v-swiper:mySwiper="swiperOption">
          <div class="swiper-wrapper">
            <div
              class="swiper-slide slide-item"
              v-for="(data, index) in agentsList"
              :key="index"
            >
              <div
                class="item-wrapper"
                v-for="(item, i) in data"
                :key="i"
                @click="routerPush(item)"
              >
                <div class="image">
                  <img :src="item.pc_thumb_url" alt />
                </div>
                <div class="font">{{ item.title }}</div>
              </div>
            </div>
          </div>
          <div class="swiper-pagination"></div>
        </div>
      </div>
      <div class="arrow-right">
        <i class="el-icon-arrow-right"></i>
      </div>
    </div>
  </div>
</template>

<script>
import { Swiper, SwiperSlide, directive } from "vue-awesome-swiper";
import { agents } from "@/api/user";
import jump from "@/utils/openWindow";
import { HaList } from "@/utils/haList";
import "swiper/css/swiper.css";
export default {
  components: {
    Swiper,
    SwiperSlide,
  },
  directives: {
    swiper: directive,
  },
  inject: ["loading"],
  data() {
    return {
      swiperOption: {
        width: 910,
        pagination: {
          el: ".swiper-pagination",
          // observer: true,
          // observeParents: true,
          // clickable: true,
          dynamicBullets: true,
        },
        navigation: {
          nextEl: ".el-icon-arrow-right",
          prevEl: ".el-icon-arrow-left",
        },
      },
      loadingList: false,
      agentsList: [],
      // listQuery: {
      //   keyword: "",
      //   page: 1,
      //   count: 4,
      //   // order: "",
      //   sort: "",
      //   type: "",
      // },
      // total: 0,
    };
  },
  watch: {},
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    },
  },
  methods: {
    async getUserInfo() {
      this.loading();
      let res = await agents();
      res.data.push({
        title: "",
        pc_thumb_url: `../../../../static/image/pc/item3.png`,
      });
      this.agentsList = await HaList(res.data);
      this.loading();
    },
    routerPush(row) {
      if (row.redirect_url) {
        jump(row.redirect_url);
      } else {
        this.$router.push({ name: "personal" });
        // let routeData = this.$router.resolve({
        //   name: "personal",
        // });
        // window.open(routeData.href, "_blank");
      }
    },
  },
  created() {
    this.$emit("customStyle", 1);
    this.$nextTick(() => {
      this.getUserInfo();
    });
  },
  mounted() {
    // console.log("Current Swiper instance object", this.mySwiper);
  },
};
</script>
<style lang="scss" scoped>
.app-container {
  position: relative;
  width: 100%;
  height: 100%;
  .head {
    z-index: -1;
    position: absolute;
    min-width: 1000px;
    width: 100%;
    height: 350px;
    background: url("../../../../static/image/pc/pc_yingyong_top.png") no-repeat;
    background-size: 100% 100%;
  }
  .content {
    padding: 100px 0 50px 0;
    position: relative;
    display: flex;
    width: 100%;
    height: 750px;
    top: 50%;
    transform: translateY(-50%);
    box-sizing: border-box;
    .arrow-left,
    .arrow-right {
      flex-grow: 1;
      display: flex;
      align-items: center;
      padding: 0 30px;
      box-sizing: border-box;
      i {
        cursor: pointer;
        font-size: 70px;
        color: #409eff;
        outline: none;
      }
    }
    .arrow-left {
      justify-content: flex-end;
    }
    .arrow-right {
      justify-content: flex-start;
    }
    .conter {
      z-index: 100;
      width: 910px;
      // margin: 0 auto;
      .swiper-container {
        width: 100%;
        height: 100%;
        padding-bottom: 50px;
        box-sizing: border-box;
        .slide-item {
          padding: 0 50px;
          box-sizing: border-box;
          display: flex;
          justify-content: space-between;
          align-content: space-between;
          flex-wrap: wrap;
          .item-wrapper {
            display: inline-flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            width: 380px;
            height: 250px;
            background-color: #ffffff;
            overflow: hidden;
            transition: 0.3s;
            border: 1px solid #ebeef5;
            border-radius: 10px 10px 6px 6px;
            cursor: pointer;
            &:hover {
              box-shadow: 0px 0px 21px 0px rgba(42, 100, 164, 0.21);
            }
            .image {
              width: 150px;
              height: 90px;
              img {
                width: 100%;
                height: 100%;
              }
            }
            .font {
              margin-top: 20px;
              font-size: 24px;
              text-indent: 1px;
              letter-spacing: 1px;
              color: #333333;
            }
          }
        }
      }
    }
  }
}
</style>


