export function HaList(arr) {
    let num = Math.ceil(arr.length / 4);
    return getList.getObjList(arr, num);
}

let getList = {
    getObjList: (arr, num) => {
        let array = [];
        let list = [];
        for (let index = 0; index < arr.length; index++) {
            //console.log(index);
            list.push(arr[index]);
            if (list.length == 4 || arr.length - 1 == index) {
                array.push(list);
                list = [];

            }

        }
        return array;
    }
}



